h1{
  margin: 0;
  text-align: center;
  border: 0.2rem solid transparent;
  font-family: sans-serif;
}


.titleTablelicense{
  font-family: sans-serif;
  margin-top: 1rem;
}

.buttonFile{
  display: none;

  
}

.containerButtonCSV{
  display: flex;
  justify-content: center;
  width: 100%;
}

.csvButton:active{
  box-shadow: 0;
  transform: scale(0.95);
  opacity: 0.8;
}
.csvButton{
  text-align: center;
  position: absolute;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
  background-color: #e1dfdf;
  border-radius: 5px;
  border: 2px solid rgb(49, 47, 47);
  outline: 1px solid #e1dfdf;
  overflow: hidden;
  width: 73px;
}

input[type=file]{
  color:transparent;
} 

.circle-handle-back-login{
  position: absolute;
  display: block;
  background-color: #616d61;
  width:100px;
  height: 100px;
  top:-50px;
  left: -50px;
  border-radius: 200px;
  transition: all 0.5s;
  z-index: 100;
}

.circle-handle-back-login:hover{
  transform:scale(2,2);
  box-shadow:
    0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072),
    0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12);
    cursor: pointer;
}

.angle-bracket-1{
  position: absolute;
  height: 2px;
  background-color: white;
  top: 63px;
  left: 59px;
  width: 18px;
  transform: rotate(311deg);
}

.angle-bracket-1::before{
  content: "";
  position: absolute;
  height: 2px;
  background-color: white;
  top: 9px;
  left: -9px;
  width: 18px;
  transform: rotate(94deg);
}

.circle-handle-table-refused {
  position: absolute;
  display: block;
  background-color: #616d61;
  width:100px;
  height: 100px;
  bottom:-50px;
  left: -50px;
  border-radius: 200px;
  transition: all 0.5s;
  z-index: 100;
}

.circle-handle-table-refused:hover{
  transform:scale(2,2);
  box-shadow:
    0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072),
    0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12);
    cursor: pointer;
}

.circle-handle-table-refused .lineLeft{
  position: absolute;
  top: 32px;
  left:-75px;
  width: 25%;
  height: 2px;
  z-index: 10;
  background-color: white;
  transform:translateX(130px) rotate(175deg);
}

.circle-handle-table-refused .lineRight{
  position: absolute;
  top: 32px;
  left: -75px;
  width: 25%;
  height: 2px;
  z-index: 10;
  background-color: white;
  transform:translateX(130px) rotate(95deg);
}


.modalLicenseRefused{
  width: 100%;
  transition: all 0.4s cubic-bezier(0.895, 0.03, 0.685, 0.22);
  z-index: -100;
}
.modalAlert{
  width: 100%;
  transition: all 0.4s cubic-bezier(0.895, 0.03, 0.685, 0.22);
  z-index: -100;
}
.open{
  opacity: 1;
  z-index: 100;
}

.closed{
  opacity: 0;
  transform: translateY(500px);
}