    .containerCard{
        width: 45%;
        height: 400px;
        background-color: #616d61;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center ;
        border-radius: 0.3rem;
        box-shadow: 0 2.8px 2.2px rgb(0 0 0 / 3%), 0 6.7px 5.3px rgb(0 0 0 / 5%), 0 12.5px 10px rgb(0 0 0 / 6%), 0 22.3px 17.9px rgb(0 0 0 / 7%), 0 41.8px 33.4px rgb(0 0 0 / 9%), 0 100px 80px rgb(0 0 0 / 12%);
    }

    .containerCard h1{
        margin-bottom: 5rem;
    }

    .containerCard button{
        margin-top: 0.5rem;
    }

    .containerCard button{
        background-color: #e1dfdf;
        border-radius: 5px;
        border: 2px solid rgb(49, 47, 47);
        outline: 1px solid #e1dfdf;
        box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
    }

    .containerCard button{
        box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
        background-color: #e1dfdf;
        border-radius: 5px;
        border: 2px solid rgb(49, 47, 47);
        outline: 1px solid #e1dfdf;
    }

    .containerCard button:active{
        box-shadow: 0;
        transform: scale(0.95);
        opacity: 0.8;
    }

    .containerInput{
        padding-top:3rem;
        width: 100%;
        display: flex;
        justify-content: center;
    }

    .containerInput label{
        font-weight: 500;
        margin-right: 10px;
        margin-bottom: 1px;
        
    }

    .containerInput input{
        width: 15%;
        left: 700px;
        margin-right: 80px;
        border-radius: 4px;
        border:2px solid black;
        outline-color:black;
        background-color: rgb(210 255 206 / 74%)

    }

    .containerInput input:focus{
        background-color: rgb(216 255 212 / 58%);
        color: black;
        opacity: 0.75; 
    }
