.containerDropdown{
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.dropDownStyle{
    background-color: #e1dfdf;
    border-radius: 5px;
    border: 2px solid rgb(49, 47, 47);
    outline: 1px solid #e1dfdf;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
}
.styleButtonDropDownlist{
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
    background-color: #e1dfdf;
    border-radius: 5px;
    border: 2px solid rgb(49, 47, 47);
    outline: 1px solid #e1dfdf;
}
.styleButtonDropDownlist:active{
    box-shadow: 0;
    transform: scale(0.95);
    opacity: 0.8;
}

