
.backdrop{
    position: absolute;
    height: 100vh;
    width: 100%;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    
}

.modal{
  display: flex;
  position: absolute;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 60%;
  background-color: rgb(182 218 178);
  /* opacity: 1; */
  border-radius: 10px;
  top: 25%;
  left: 20%;
}

.modal-alert{
  display: flex;
  position: absolute;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 30%;
  background-color:rgb(182 218 178);
  opacity: 1;
  border-radius: 10px;
  top:25%;
  left: 35%;
}

.buttonModal:active{
    box-shadow: 0;
    transform: scale(0.95);
    opacity: 0.8;
  }
  .buttonModal{
    text-align: center;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
    background-color: #e1dfdf;
    border-radius: 5px;
    border: 2px solid rgb(49, 47, 47);
    outline: 1px solid #e1dfdf;
    width: 73px;
    margin-bottom: 0.2rem;
  }

