.tableContainer{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.tableStyle{
    width: 80%;
    max-width: 100%;
    margin-bottom: 0.3rem;
    background-color: transparent;
    border-spacing: 2px;
    border-color: grey;
    box-sizing: border-box;
    background-color: #616d61;
    color: white;
    box-shadow:
    0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072),
    0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12);
    border-radius: 5px;
}

.tableStyle tbody{
    display:block;
    height: 200px;
    overflow-y:scroll;
}

.tableStyle thead, .tableStyle tbody tr {
    display:table;
    width:100%;
    table-layout:fixed;
}


.tableStyle th, .tableStyle td {
    padding: 0.75rem;
    vertical-align: top;
    text-align: center;
    border-top: 1px solid #e1dfdf;
}

.tableContainer .titleTable{
    border:0;
    font-family: sans-serif;
}

.emptyContent{
    display: flex;
    justify-content: center;
    width:50%;
    margin-top: 5rem;
    margin-bottom: 43rem;
    border:none;
    font-family: sans-serif;
    font-size: 20px;
    font-weight: 500;
}